.inbottom-settle {
  @include animation-base(inbottom-settle, 500ms); }
@include keyframes-custom(inbottom-settle) {
  0% {
    opacity: 0;
    @include transform(translateY(30px)); }
  50% {
    opacity: 1;
    @include transform(translateY(-5px)); }
  70% {
    opacity: 1;
    @include transform(translateY(2px)); }
  90% {
    opacity: 1;
    @include transform(translateY(-1px)); }
  100% {
    opacity: 1;
    @include transform(translateY(0px)); } }

.fadein-left {
  @include animation-base(fadein-left, 1000ms); }
@include keyframes-custom(fadein-left) {
  0% {
    opacity: 0;
    @include transform(translateX(-50px)); }
  100% {
    opacity: 1;
    @include transform(translateX(0px)); } }

.inleft-custom {
  @include animation-base(inleft-custom, 200ms); }
@include keyframes-custom(inleft-custom) {
  0% {
    opacity: 0;
    @include transform(translateX(-30px)); }
  100% {
    opacity: 1;
    @include transform(translateX(0px)); } }

.inleft-slow {
  @include animation-base(inleft-slow, 500ms); }
@include keyframes-custom(inleft-slow) {
  0% {
    opacity: 0;
    @include transform(translateX(-30px)); }
  100% {
    opacity: 1;
    @include transform(translateX(0px)); } }

.zoom-enter {
  @include animation-base(zoom-enter, 500ms, ease-in); }
@include keyframes-custom(zoom-enter) {
  0% {
    opacity: 0;
    @include transform(scale(0.1,0.1)); }
  50% {
    opacity: 1;
    @include transform(scale(1.07,1.07)); }
  70% {
    opacity: 1;
    @include transform(scale(0.95,0.95)); }
  90% {
    opacity: 1;
    @include transform(scale(1.02,1.02)); }
  100% {
    opacity: 1;
    @include transform(scale(1,1)); } }

.zoom-enter-slow {
  @include animation-base(zoom-enter-slow, 700ms, ease-in); }
@include keyframes-custom(zoom-enter-slow) {
  0% {
    opacity: 0;
    @include transform(scale(0.1,0.1)); }
  50% {
    opacity: 1;
    @include transform(scale(1.1,1.1)); }
  70% {
    opacity: 1;
    @include transform(scale(0.95,0.95)); }
  90% {
    opacity: 1;
    @include transform(scale(1.05,1.05)); }
  100% {
    opacity: 1;
    @include transform(scale(1,1)); } }

.zoom-enter-plain {
  @include animation-base(zoom-enter-plain, 300ms, ease-in); }
@include keyframes-custom(zoom-enter-plain) {
  0% {
    opacity: 0;
    @include transform(scale(0.8,0.8)); }
  100% {
    opacity: 1;
    @include transform(scale(1,1)); } }

.intop-settle {
  @include animation-base(intop-settle, 600ms, ease-in); }
@include keyframes-custom(intop-settle) {
  0% {
    @include transform(translateY(-144px)); }
  70% {
    @include transform(translateY(-15px)); }
  90% {
    @include transform(translateY(-5px)); }
  60%,80%,100% {
    @include transform(translateY(0px)); } }

.inbtm-settle {
  @include animation-base(inbtm-settle, 600ms, ease-in); }
@include keyframes-custom(inbtm-settle) {
  0% {
    @include transform(translateY(144px)); }
  70% {
    @include transform(translateY(15px)); }
  90% {
    @include transform(translateY(5px)); }
  60%,80%,100% {
    @include transform(translateY(0px)); } }

.title-sequence {
  @include animation-base(title-sequence, 3000ms, ease-in-out); }
@include keyframes-custom(title-sequence) {
  0% {
    opacity: 0;
    @include transform(translateY(-20px)); }
  10% {
    opacity: 1;
    @include transform(translateY(0px)); }
  90% {
    opacity: 1;
    @include transform(translateY(0px)); }
  100% {
    opacity: 0;
    @include transform(translateY(20px)); } }
